import React, { useContext, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Link, useParams } from 'react-router-dom';

import WebContext from '../../../application/contexts/WebContext';
import Global from '../../../infrastructure/Common/Global';
import ClientApis from '../APIs/ClientApis';

import { ReactComponent as AssetBackArrow } from "./../../../assets/images/icons/back-arrow.svg";

const Partner = () => {
    const { t } = useTranslation();
    const webCtx = useContext(WebContext);
    const params = useParams();


    const [isLoading, setIsLoading] = useState(true);
    const [partner, setPartner] = useState({});

    const baseURL = Global.baseUrl + "/";

    useEffect(() => {
        if (params.partnerId) {
            getPartner(params.partnerId);
        }
    }, []);

    const getPartner = (partnerId) => {
        ClientApis.partner(partnerId)
            .then(res => {
                // setTimeout(() => {
                setPartner(res.data.result);
                setIsLoading(false);
                // }, 4000);
            })
            .catch(err => {
                console.log(err);
                setIsLoading(false);
            })
    };

    return (
        <div className="">

            <section className="partner-meta py-5">
                <div className="container-xxl">
                    <div className="row">
                        <div className="col-md-8" data-aos="fade-up" data-aos-duration={500} data-aos-easing="linear" data-aos-delay={500}>
                            {isLoading ?
                                <div className="content-placeholder">
                                    <h1 className="placeholder-glow mb-4">
                                        <span className="placeholder col-11 h-25"></span>
                                        <span className="placeholder col-5 mt-3"></span>
                                    </h1>
                                    <p className="card-text placeholder-glow">
                                        <span className="placeholder col-12 mt-3"></span>
                                        <span className="placeholder col-6"></span>
                                        <br />
                                        <span className="placeholder col-5 my-3"></span>
                                        <br />
                                        <span className="placeholder col-4"></span>
                                    </p>
                                </div>
                                :
                                <React.Fragment>
                                    <Link className="return-back-textual mb-2" to="/partners">
                                        <AssetBackArrow className="me-1" />
                                        <span>{t('menu_partners')}</span>
                                    </Link>

                                    <h1 className="title fw-medium mb-0">{partner['title_' + webCtx.appLang]}</h1>
                                    <div className="my-3">
                                        <div className="render-content" dangerouslySetInnerHTML={{ __html: partner[`content_${webCtx.appLang}`] }} />
                                    </div>

                                    <div className="my-3">
                                        <span className="max-text">{t('ceo')}:</span> <span className="underline fw-medium">{partner.author}</span>
                                    </div>

                                    {partner.custom_fields.map((field, indx) => (
                                        <div className="my-3" key={indx}>
                                            <span className="max-text">{t(field.key)}:</span> {field.value}
                                        </div>
                                    ))}

                                </React.Fragment>
                            }
                        </div>
                        <div className="col-md-4" data-aos="fade-up" data-aos-duration={500} data-aos-easing="linear" data-aos-delay={1000}>
                            <div className="img-container">

                                {isLoading ?
                                    <div className="content-placeholder">
                                        <h5 className="w-100 min-h-200 placeholder-glow mt-2 me-3">
                                            <span className="placeholder col-12 min-h-250"></span>
                                        </h5>
                                    </div>
                                    : <div className="partner-img" style={{ backgroundImage: `url('${baseURL + partner.featured_url}')` }}></div>}
                            </div>
                        </div>
                    </div>
                </div>
            </section>

        </div>
    );
};

export default Partner;