import React, { useContext, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';

import WebContext from '../../../application/contexts/WebContext';

import ClientApis from '../APIs/ClientApis';

import Global from '../../../infrastructure/Common/Global';
import DataNotFound from '../../../infrastructure/Common/DataNotFound';

// import './partners.scss';

const PartnersList = () => {
    const { t } = useTranslation();
    const webCtx = useContext(WebContext);

    const [isLoading, setIsLoading] = useState(true);
    const [partners, setPartners] = useState([]);

    const baseURL = Global.baseUrl + "/";
    // const baseURL = 'https://it-be.mytextbook.net/';

    useEffect(() => {
        getCourses();
    }, []);

    const getCourses = () => {
        ClientApis.partners()
            .then(res => {
                // setTimeout(() => {
                setPartners(res.data.result.posts);
                setIsLoading(false);
                // }, 2000);
            })
            .catch(err => {
                console.log(err);
                setIsLoading(false);
            })
    };

    return (
        <div className="container-xxl py-5">

            <div className="d-block d-md-flex align-items-end justify-content-between">
                <div className="section-header">
                    {/* <div className="primary">{t('menu_courses')}</div> */}
                    <h2 className="title fw-medium mb-0">{t('partners_title')}</h2>
                    <div>{t('partners_desc')}</div>
                </div>
            </div>

            <section>
                <div className="section-content mt-5">
                    <div className="row">
                        {isLoading ?
                            Array.from({ length: 2 }, (_, index) => (
                                <div className="col-lg-6 col-md-12 mb-3" key={index}>
                                    <div className="nodis-partner p-3 h-100 content-placeholder d-flex" key={index}>
                                        <h5 className="sq-200 placeholder-glow mt-2 me-3">
                                            <span className="placeholder col-12 sq-200 rounded-1"></span>
                                        </h5>
                                        <div className="content-placeholder mt-1 w-100">
                                            <h6 className="placeholder-glow mb-4">
                                                <span className="placeholder col-5"></span>
                                            </h6>
                                            <div className="card-text placeholder-glow">
                                                <span className="placeholder col-12 mt-12"></span>
                                                <span className="placeholder col-9 mt-12"></span><br />
                                                <span className="placeholder col-3 mt-12"></span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            ))
                            :
                            <>
                                {partners && partners.length ?
                                    <>
                                        {partners.map((item, index) => (
                                            <div className="col-lg-6 col-md-12 mb-3" key={index}>
                                                <div className="nodis-partner p-3 mb-2 h-100 d-md-flex">
                                                    <div className="d-flex align-items-end me-md-3 min-h-200 min-w-200 h-100">
                                                        <div className="poster-container min-h-200 min-w-200" style={{ backgroundImage: `url('${baseURL + item.featured_url}')` }}></div>
                                                    </div>

                                                    <div className="content mt-3 mt-md-0 w-100 h-100 justify-content-between">
                                                        <div>
                                                            <Link to={'/partners/' + item.id} className="title fw-medium">{item['title_' + webCtx.appLang]}</Link>
                                                            <div className="desc mt-2">
                                                                {/* {item['content_' + webCtx.appLang]} */}
                                                                <div className="render-content" dangerouslySetInnerHTML={{ __html: item[`content_${webCtx.appLang}`] }} />
                                                            </div>
                                                        </div>

                                                        <div className="text-start">
                                                            <Link to={'/partners/' + item.id} className="max-text small pointer">
                                                                {t('view_detail')}
                                                            </Link>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        ))}
                                    </>

                                    : null}
                            </>
                        }
                        {!isLoading && !partners.length && <DataNotFound />}
                    </div>
                </div>
            </section>

        </div>
    );
};

export default PartnersList;